import React, { useContext } from "react";
import { AuthContext } from "../../context";
// import "./styles.css";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  InputLabel,
  makeStyles,
  Modal,
  TextField,
} from "@material-ui/core";

const useStyle = makeStyles((theme) => ({
  button: {
    position: "absolute",
    right: 0,
    top: 0,
  },
  dialog: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    justifyItems: "center",
    "& > *": {
      margin: theme.spacing(2),
    },
  },
}));

export default function Authentication() {
  const classes = useStyle();
  const authContext = useContext(AuthContext);
  const [userInput, setUserInput] = React.useState({
    username: "",
    password: "",
  });
  console.log(authContext.username);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const loginHandler = () => {
    console.log(authContext.username);
    if (userInput.username === "das777" && userInput.password === "das210602") {
      authContext.login();
    } else {
      alert("Error");
    }
  };

  const logoutHandler = () => {
    authContext.logout();
  };

  const handleChange = (props) => (event) => {
    setUserInput({ ...userInput, [props]: event.target.value });
  };

  return (
    <>
      {!authContext.isLoggedIn && (
        <>
          <Button variant="outlined" className={classes.button} onClick={handleOpen}>
            Login
          </Button>
          <Dialog open={open} onClose={handleClose}>
            <DialogTitle>Login</DialogTitle>
            <DialogContent className={classes.dialog}>
              <TextField
                value={userInput.username}
                label="User Name"
                onChange={handleChange("username")}
              />
              <TextField
                type="password"
                value={userInput.password}
                label="Password"
                onChange={handleChange("password")}
              />
              <Button onClick={loginHandler} variant="contained" color="primary">
                Login
              </Button>
              <Button onClick={handleClose} variant="contained">
                Cancel
              </Button>
            </DialogContent>
          </Dialog>
        </>
      )}
      {authContext.isLoggedIn && (
        <Button
          variant="contained"
          color="secondary"
          className={classes.button}
          onClick={logoutHandler}
        >
          Logout
        </Button>
      )}
    </>
  );
}
