import React from "react";
import { Link } from "react-router-dom";

import {
  AppBar,
  Paper,
  Toolbar,
  Button,
  MenuItem,
  Select,
  Typography,
  FormControl,
  InputLabel,
  CardMedia,
  Card,
  Divider,
  Radio,
  FormControlLabel,
  FormLabel,
} from "@material-ui/core";

import {
  AccountBalanceWallet as TabIcon,
  LocalBar as BarIcon,
  LocalCafe as CafeIcon,
} from "@material-ui/icons";

import { useStyles } from "./styles";

//components
import Rating from "../../components/Rating";
import { getMonday } from "../../helpers";

//images
import { queenstownData } from "../../assets/images/queenstown";
import Logo from "../../assets/images/logo.png";
import banner from "../../assets/images/banner.jpg";

export default function RegionSearch() {
  const classes = useStyles();

  const subRegions = [
    "ArrowTown",
    "Arthurs Point",
    "Frankton",
    "Gibbston",
    "Lake Hayes",
    "Queenstown",
  ];

  // populate locations
  let LocationsList = [];

  for (const region of subRegions) {
    LocationsList.push(<MenuItem>{region}</MenuItem>);
    // console.log(key, LocationsKey[key]);
  }

  //populate date for drop down
  var dateFormat = require("dateformat");
  var now = new Date();
  let Dates = [];
  Dates.push(<MenuItem value={0}>Today</MenuItem>);
  for (let i = 1; i < 10; i++) {
    // console.log(dateFormat(now, "dd / mm"));
    let date = new Date(Date.now() - i * 24 * 60 * 60 * 1000);
    Dates.push(<MenuItem>{dateFormat(date, "ddd dd mmm")}</MenuItem>);
  }

  //populate date of the week for bookings
  const weekBegin = getMonday(now);
  let Bookings = [];
  for (let i = 0; i <= 6; i++) {
    let date = new Date(Date.now() + i * 24 * 60 * 60 * 1000);
    Bookings.push({ day: dateFormat(date, "ddd"), date: dateFormat(date, "dd/mm") });
  }

  console.log(Bookings);
  return (
    <div className="column homeLayout">
      {/* <AppBar color="transparent" className="column"> */}
      <div>
        <img className={classes.logo} src={Logo} alt="" srcset="" />
      </div>
      <Toolbar className="row">
        <MenuItem className="column">
          <BarIcon />
          <Typography>Bar</Typography>
        </MenuItem>
        <MenuItem className="column">
          <TabIcon />
          <Typography>Tab</Typography>
        </MenuItem>
        <MenuItem className="column">
          <CafeIcon />
          <Typography>Coffee</Typography>
        </MenuItem>
      </Toolbar>
      {/* </AppBar> */}

      <div>
        <img className={classes.banner} src={banner} alt="" srcset="" />
      </div>
      <div className="row">
        <Paper className={classes.paperForm}>
          <FormControl className={classes.formControl}>
            <InputLabel>Date</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
              defaultValue={0}
            >
              {Dates}
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel>Location</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
            >
              {LocationsList}
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel>Time</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
            >
              <MenuItem>Morning</MenuItem>
              <MenuItem>Afternoon</MenuItem>
              <MenuItem>Evening</MenuItem>
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <Button variant="contained" color="primary">
              Find a venue
            </Button>
          </FormControl>
        </Paper>
      </div>

      <div className={classes.content}>
        <div>
          {queenstownData.map((venue) => (
            <Paper className={classes.paperCard} elevation={3}>
              <Card>
                <div className="row">
                  <CardMedia image={venue.image} classes={{ root: classes.cardImageRoot }} />
                  <div className={`column ${classes.bookingContent}`}>
                    <Typography variant="h5">{venue.title}</Typography>
                    <Rating
                      rating={Math.round((Math.random() + 2) * 1.5)}
                      numReviews={Math.round(Math.random() * 20)}
                    />
                    <div className="row">
                      {venue.subLocation} <span>{venue.cuisine}</span>
                    </div>
                    <div className="row">
                      {Bookings.map((item) => (
                        <div
                          className={`column ${classes.bookingDay} ${
                            Math.round(Math.random()) === 0 ? "active" : ""
                          }`}
                        >
                          <span>{item.day}</span>
                          <Divider variant="middle" />
                          <span>{item.date}</span>
                          <span>6PM</span>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </Card>
            </Paper>
          ))}
        </div>

        <FormControl classes={{ root: classes.option }}>
          <FormLabel>Cuisine</FormLabel>
          <FormControlLabel control={<Radio />} label="Thai" />
          <FormControlLabel control={<Radio />} label="Vietnamese" />
          <FormControlLabel control={<Radio />} label="Italian" />
          <FormControlLabel control={<Radio />} label="Steak" />
          <FormControlLabel control={<Radio />} label="Cocktail" />
          <FormLabel>Atmostphere</FormLabel>
          <FormControlLabel control={<Radio />} label="Bar" />
          <FormControlLabel control={<Radio />} label="Karaoke" />
          <FormControlLabel control={<Radio />} label="Lounge" />
          <FormControlLabel control={<Radio />} label="Live Band" />
          <FormControlLabel control={<Radio />} label="Sports Bar" />
          <FormControlLabel control={<Radio />} label="Pet Friendly" />
          <FormLabel>Facilities</FormLabel>
          <FormControlLabel control={<Radio />} label="Pool" />
          <FormControlLabel control={<Radio />} label="Parking Available" />
          <FormControlLabel control={<Radio />} label="Meeting Facilities" />
          <FormControlLabel control={<Radio />} label="Free Wifi" />
          <FormControlLabel control={<Radio />} label="Smoking Area" />
        </FormControl>
      </div>
    </div>
  );
}
