import React, { useContext, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../../context";

import {
  AppBar,
  Toolbar,
  Button,
  Icon,
  IconButton,
  MenuItem,
  Select,
  Typography,
  FormControl,
  InputLabel,
  CardMedia,
  Grid,
  Card,
  Paper,
  FormHelperText,
} from "@material-ui/core";

import { Facebook, Instagram, Twitter } from "@material-ui/icons";

import { useStyles } from "./styles";

//images

import * as LocationsIcon from "../../assets/images/locations";
import Logo from "../../assets/images/logo.png";

export default function Home() {
  const authContext = useContext(AuthContext);

  const classes = useStyles();
  const [isSticky, setSticky] = useState(false);
  const element = useRef(null);

  // populate locations
  let Locations = [];
  let LocationsList = [];
  const LocationsKey = Object.keys(LocationsIcon);

  for (const key of LocationsKey) {
    Locations.push(
      <Grid item xs={6} sm={6} md={4} lg={3}>
        <Link to="/queenstown">
          <Paper elevation={5} className={classes.paperCard}>
            <Card className={classes.card}>
              <CardMedia image={LocationsIcon[key]} classes={{ root: classes.cardImage }} />
              <Typography variant="h5" className={classes.cardTitle}>
                {key}
              </Typography>
            </Card>
          </Paper>
        </Link>
      </Grid>
    );
    LocationsList.push(<MenuItem>{key}</MenuItem>);
    // console.log(key, LocationsKey[key]);
  }

  //populate date
  var dateFormat = require("dateformat");
  var now = new Date();
  const Dates = [];
  Dates.push(<MenuItem value={0}>Today</MenuItem>);
  for (let i = 1; i < 10; i++) {
    // console.log(dateFormat(now, "dd / mm"));
    let date = new Date(now - i * 24 * 60 * 60 * 1000);
    Dates.push(<MenuItem>{dateFormat(date, "dd / mm")}</MenuItem>);
  }

  const handleScroll = () => {
    window.scrollY > element.current.getBoundingClientRect().bottom
      ? setSticky(true)
      : setSticky(false);
  };

  // This function handles the scroll performance issue

  const debounce = (func, wait = 20, immediate = true) => {
    let timeOut;
    return () => {
      let context = this,
        args = arguments;
      const later = () => {
        timeOut = null;
        if (!immediate) func.apply(context, args);
      };
      const callNow = immediate && !timeOut;
      clearTimeout(timeOut);
      timeOut = setTimeout(later, wait);
      if (callNow) func.apply(context, args);
    };
  };

  return (
    authContext.isLoggedIn && (
      <div className="column">
        {/* <AppBar color="transparent" className="column"> */}
        <div>
          <img className={classes.logo} src={Logo} alt="" srcset="" />
        </div>
        <div>
          <Button className={classes.iconButton} variant="contained" color="secondary">
            Bar
          </Button>
          <Button className={classes.iconButton} variant="contained" color="secondary">
            Tab
          </Button>
          <Button className={classes.iconButton} variant="contained" color="secondary">
            Coffee
          </Button>
        </div>
        {/* <Toolbar className="row">
          <MenuItem className="column">
            <BarIcon />
            <Typography>Bar</Typography>
          </MenuItem>
          <MenuItem className="column">
            <TabIcon />
            <Typography>Tab</Typography>
          </MenuItem>
          <MenuItem className="column">
            <CafeIcon />
            <Typography>Coffee</Typography>
          </MenuItem>
        </Toolbar> */}
        {/* </AppBar> */}

        <div className="row">
          <FormControl className={classes.formControl}>
            <InputLabel>Date</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
              defaultValue={0}
            >
              {Dates}
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel>Location</InputLabel>
            <Select
              defaultValue={""}
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
            >
              <MenuItem disabled value="">
                Helper
              </MenuItem>
              {LocationsList}
            </Select>
          </FormControl>
          <FormControl className={classes.formControl}>
            <InputLabel>Time</InputLabel>
            <Select
              MenuProps={{
                anchorOrigin: {
                  vertical: "bottom",
                  horizontal: "left",
                },
                transformOrigin: {
                  vertical: "top",
                  horizontal: "left",
                },
                getContentAnchorEl: null,
              }}
            >
              <MenuItem>Morning</MenuItem>
              <MenuItem>Afternoon</MenuItem>
              <MenuItem>Evening</MenuItem>
            </Select>
          </FormControl>
          <FormControl>
            <Button variant="contained" color="primary">
              Find a venue
            </Button>
          </FormControl>
        </div>

        <Grid container spacing={0}>
          {Locations}
          {/* <Grid item>
          <Card>
            <CardMedia image={LocationsIcon.Auckland} classes={{ root: classes.cardImage }} />
          </Card>
        </Grid> */}
        </Grid>
        <div className={`row ${classes.footerBottom}`}>
          <div className="column">
            <Typography variant="h4">Diners</Typography>
            <Typography variant="body1">Coming and join us for exciting deals</Typography>
          </div>
          <div className="column">
            <Typography variant="h4">Restauranteurs</Typography>
            <Typography variant="body1">
              Fill those empty seats and increase your revenue
            </Typography>
          </div>
          <div className="column">
            <Typography variant="h4">Follow Us</Typography>
            <div className="row">
              <Facebook />
              <Instagram />
              <Twitter />
            </div>
          </div>
        </div>
        <div>
          <Toolbar>
            <MenuItem>Home</MenuItem>
            <MenuItem>FAQ</MenuItem>
            <MenuItem>Contact Us</MenuItem>
            <MenuItem>Privacy Policy</MenuItem>
            <MenuItem>Our Story</MenuItem>
            <MenuItem>Restauranteurs</MenuItem>
          </Toolbar>
        </div>
        <Typography>Copyright &#169; 2020 by First Drink Ltd</Typography>
      </div>
    )
  );
}
